import Head from "next/head";
import { useState } from "react";
import { useRouter } from "next/router";

export default function SignUp() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [message, setMessage] = useState("");
  const router = useRouter();

  async function submit(e) {
    e.preventDefault();
    setMessage("");
    const res = await fetch("/api/auth/signup", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ email, password })
    });
    const data = await res.json();
    if (data.ok) {
      router.push("/login");
    } else {
      setMessage(data.message || "Signup failed");
    }
  }

  return (
    <main className="auth-page">
      <Head><title>Create account — Staff Scheduler</title></Head>
      <div className="auth-card">
        <div style={{ display: "flex", alignItems: "center", gap: 12 }}>
          <div className="auth-logo">SO</div>
          <div>
            <h1 className="auth-title">Create account</h1>
            <div className="auth-sub">Join the team — create your staff account</div>
          </div>
        </div>

        <form onSubmit={submit} style={{ marginTop: 12 }}>
          <div className="form-row">
            <label className="form-label">Email</label>
            <input className="form-input" value={email} onChange={(e) => setEmail(e.target.value)} placeholder="your@email.com" />
          </div>
          <div className="form-row">
            <label className="form-label">Password</label>
            <input className="form-input" type="password" value={password} onChange={(e) => setPassword(e.target.value)} placeholder="Choose a password" />
          </div>
          {message && <div className="error">{message}</div>}

          <div className="auth-actions">
            <button className="btn full">Create account</button>
            <a className="muted-link" href="/login">Already have an account?</a>
          </div>
        </form>
      </div>
    </main>
  );
}


